'use strict';

/**
* Test if a value is `NaN`.
*
* @module @stdlib/assert-is-nan
*
* @example
* import isnan from '@stdlib/assert-is-nan' ;
*
* var bool = isnan( NaN );
* // returns true
*
* bool = isnan( new Number( NaN ) );
* // returns true
*
* bool = isnan( 3.14 );
* // returns false
*
* bool = isnan( null );
* // returns false
*
* @example
* import { isPrimitive as isnan } from '@stdlib/assert-is-nan' ;
*
* var bool = isnan( NaN );
* // returns true
*
* bool = isnan( 3.14 );
* // returns false
*
* bool = isnan( new Number( NaN ) );
* // returns false
*
* @example
* import { isObject as isnan } from '@stdlib/assert-is-nan' ;
*
* var bool = isnan( NaN );
* // returns false
*
* bool = isnan( new Number( NaN ) );
* // returns true
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import main from './main.js' ;
import isPrimitive from './primitive.js' ;
import isObject from './object.js' ;


// MAIN //

setReadOnly( main, 'isPrimitive', isPrimitive );
setReadOnly( main, 'isObject', isObject );


// EXPORTS //

export default main;
